const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			info: {
				id: '1',
				title: '有钱花二次贷，只要有信用卡或者有公积金就 来，人均10000起步，秒批最高10万，当天到 账',
				img: '../static/logo.png',
				name: 'admin',
				type: '楼主',
				time: '1小时之前'
			},
			nodes: [{
				name: 'div',
				attrs: {
					class: 'div-class',
					style: 'color: #333333;font-size:14px;line-height:20px;'
				},
				children: [{
					type: 'text',
					text: '公告年终业绩每股收入是:3.98元，（即是第四季度收入是:1.87元的）；2017年前三3季度每股收入业绩是:1.99元，公告'
				}]
			}],
			commentList: [{
					id: '1',
					img: '../static/logo.png',
					name: 'admin',
					content: '写的不错，可以试试！',
					time: '9分钟之前',
					floor: '1'
				},
				{
					id: '1',
					img: '../static/logo.png',
					name: 'adminadminadmin',
					content: '写的不错，xxxxxxxx可以试试！',
					time: '9分钟之前',
					floor: '1'
				}
			],
			articleDetail: null,
			options: null,
			imgUrl: '',
		}
	},
	onLoad(options) {
		this.options = options
		this.imgUrl = app.globalData.imgUrl
		// 文章详情加载
		this.getArticleDetail()
	},
	methods: {
		// 文章详情加载
		getArticleDetail() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/posts/show`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					post_id: this.options.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						this.articleDetail = res.data
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			})
		},
		shareFriend() {
			//分享到微信朋友
			this.share('WXSceneSession')
		},
		shareFriendcricle() {
			//分享到微信朋友圈
			this.share('WXSenceTimeline')
		},
		share(WXSenceType) {
			console.log(window.location.href)
			uni.share({
				provider: "weixin",
				scene: WXSenceType,
				type: 0,
				href: window.location.href,
				title: this.articleDetail.title,
				summary: "唐艺昕，没有水的地方叫沙漠，没有你的地方，你知道叫什么吗？不知道。叫寂寞。",
				imageUrl: "http:*******************",
				success: function(res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function(err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		// 文章--添加--收藏
		addCollection() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/add_collection`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					post_id: this.articleDetail.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						// 修改帖子的收藏状态
						this.articleDetail.is_collections == 1
						uni.showToast({
							title: res.data.message
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			})
		},
		// 文章--取消--收藏
		delCollection() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/del_collection`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					post_id: this.articleDetail.id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						// 修改帖子的收藏状态
						this.articleDetail.is_collections == 0
						uni.showToast({
							title: res.data.message
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			})
		},
		addFollow() {
			// 关注用户
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/add_follow`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					follow_id: this.articleDetail.user_id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						uni.showToast({
							title: res.data.message
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			})
		}
	}
	
};
